import React from 'react'
import { Link } from "gatsby"
import { useForm } from "@formspree/react";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";

export default function ContactForm () {

  const { executeRecaptcha } = useGoogleReCaptcha();

  const [state, handleSubmit] = useForm(`${process.env.GATSBY_FORMSPREE_FORM_ID}`, {
    data: { "g-recaptcha-response": executeRecaptcha }
  })

  return (
    <div>
        <form
          className="contact-form"
          name="contact"
          onSubmit={handleSubmit}
        >
          <div>
            <label htmlFor="name">
              Imi i nazwisko
            </label>
            <input className="text-input" type="text" name="name" id="name" required={true} />
          </div>
          <div>
            <label htmlFor="email">
              Email
            </label>
            <input className="text-input" type="email" name="email" id="email" required={true} />
          </div>
          <div>
            <label htmlFor="phone">
              Nr telefonu
            </label>
            <input className="text-input" type="text" name="phone" id="phone" required />
          </div>
          <div>
            <label htmlFor="name">
              Temat
            </label>
            <input className="text-input" type="text" name="subject" id="subject" required />
          </div>
          <div>
            <label htmlFor="message">
              Wiadomo
            </label>
            <textarea name="message" id="message" required />
          </div>
          <div className="checkbox-field">
            <input
              type="checkbox"
              name="consent-to-contact"
              id="consent-to-contact"
              required 
            />
            <label htmlFor="consent-to-contact">Na podstawie art. 10 ust. 2 ustawy o wiadczeniu usug drog elektroniczn wyraam zgod na przekazywanie przez AOperat Prosta Spka Akcyjna, na udostpniony przeze mnie adres e-mail informacji handlowych.</label>
          </div>
          <div className="checkbox-field">
            <input
                type="checkbox"
                name="accepted-terms"
                id="accepted-terms"
                required
              />
            <label htmlFor="accepted-terms">Owiadczam, e zapoznaem(am), si z treci Regulaminu oraz Polityki Prywatnoci i akcptuj zawarte w nich postanowienia.</label>
          </div>
          <div className="mrb-text-div mt-6">
            Przed wyraeniem zgody prosimy o zapoznanie si z <Link to="/terms-and-conditions">Regulaminem</Link> i <Link to="/privacy-policy">Polityk Prywantoci</Link>. Wyraenie zgody na kontakt drog mailow i zaakceptowanie postanowie Polityki Prywatnoci oraz Regulaminu jest dobrowolne ale niezbdne do tego abymy mogli si z Tob skontaktowa. Zgod mona w kadej chwili cofn kontaktujc si z Administratorem Danych Osobwych wskazanym w <Link to="/privacy-policy">Polityce Prywantoci</Link>. 
          </div>
          <div className="text-align-right">
            <button
              className="mt-3 button mrb-button mrb-button-light is-full-width"
              sx={{
                variant: "variants.button",
              }}
              type="submit"
              disabled={state.submitting}
            >
              Wylij wiadomo
            </button>
            { state.succeeded === true && 
              <p className="is-color-primary-green is-centered mt-3">Wiadomo zostaa wysana</p>
            }
          </div>
        </form>
      </div>
  )
}